<template>
  <div
    class="page-post"
    :class="{ 'is-popup-showing': isPopupShowing }"
  >
    <transition
      name="transition-header"
      :duration="{ enter: 2000, leave: 600 }"
    >
      <div
        v-if="isPopupShowing"
        class="page-news__header"
      >
        <the-header-inner/>
      </div>
    </transition>

    <div class="page-post__root">
      <div id="page-post__wrapp" ref="contentWrap" class="page-post__wrapp">
        <the-header-new />

        <div class="page-post__main">

          <div class="page-post__main-wrapper">
            <div class="page-post__main-content">

              <div class="page-post__main-article">

                <div class="page-post__heading">
                  <router-link :to="{ name: 'news' }" class="page-post__back"></router-link>
                  <div class="page-post__heading-date">December 09, 2018</div>
                  <div class="page-post__heading-main">No more listings!</div>
                </div>

                <div class="page-post__text">
                  <p>You can now trade ANY ERC20 token on EtherMium DEX! No listings! Simply search any token by name or
                    address. The search pulls up results from EtherScan.io so if your token is not registered on
                    EtherScan.io, search by the token address.
                  </p>

                  <p>Happy trading!</p>
                </div>
              </div>

              <div class="page-post__main-sidebar">
                <div class="page-post__heading-main">Other News</div>
                <div class="page-post__sidebar-post">
                  <div class="page-post__heading-date">September 13, 2018</div>
                  <router-link :to="{ name: 'postsecond' }" class="page-post__sidebar-post-title">Ledger Nano S wallet now supported</router-link>
                </div>
                <div class="page-post__sidebar-post">
                  <div class="page-post__heading-date">June 11, 2018</div>
                  <router-link :to="{ name: 'postthird' }" class="page-post__sidebar-post-title">Platform Launch</router-link>
                </div>
              </div>
            </div>
          </div>
        </div>

        <the-main-info/>
        <the-footer/>
      </div>
    </div>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
/**
 * Component: Страница поста
 *
 * @version 2.0.0
 */


import { mapGetters } from 'vuex';
import { chatMixin } from '@/util/mixins';

import iconArrow from '@/icons/e-icon_arrow.svg';
import iconBack from '@/icons/e-icon__back-big.svg';

// Components
import TheTokenSearchEtherscanMobile from '@/containers/blocks/TheTokenSearchEtherscanMobile';
import EBigNumber from '@/components/EBigNumber';
import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import TheFooter from '@/containers/blocks/TheFooter';
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheHeaderNew from '@/containers/blocks/TheHeaderNew';
import TheMainInfo from '@/containers/blocks/TheMainInfo';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMobileTokenSearch from '@/containers/blocks/TheMobileTokenSearch';
import TheTokenSearchResultNew from '@/containers/blocks/TheTokenSearchResultNew';


export default {
  name: 'page-nomorelistings',

  data() {
    return {
      iconArrow,
      iconBack,
    };
  },

  computed: {
    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
    }),
  },

  async mounted() {
    this.$ga.page(this.$router);
    this.$enableChat();
  },

  components: {
    TheTokenSearchEtherscanMobile,
    TheMobileTokenSearch,
    TheTokenSearchResultNew,
    EBigNumber,
    EButton,
    EIcon,
    TheFooter,
    TheHeaderInner,
    TheHeaderNew,
    TheMainInfo,
    TheMenuSidebar,
    ThePopup,
  },

  mixins: [chatMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.page-post {
  $this: &;

  &__root {
    font-weight: 500;
    background-color: #f2f2f2;
    width: 100%;
    height: 100vh;
    overflow: hidden;
  }

  &__wrapp {
    overflow-y: auto;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }

  &__header {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    height: 78px;
    z-index: 102;
  }

  // MAIN section
  // ==================================================
  &__main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-color: #031472;
    position: relative;
    margin-top: -120px;
    padding-top: 120px;
    z-index: 0;

    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      z-index: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(to bottom, rgba(5,4,26,.5) 0%,rgba(5,4,26,.4) 30%, rgba(5,4,26,0) 70%, rgba(5,4,26,0) 100%);
    }

    &::before {
      content: '';
      position: absolute;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 0;
      bottom: -120px;
      width: 100%;
      max-width: 1600px;
      height: 180px;
      background: #030C52;
      border-radius: 50%;
      filter: blur(30px);
      opacity: .4;

      @include r(992) {
        display: none;
      }
    }
  }

  &__main-wrapper {
    background: #f2f2f2;
    z-index: 1;
  }

  &__main-content {
    width: 100%;
    max-width: 1440px;
    padding: 70px 130px 190px;
    box-sizing: border-box;
    margin: 0 auto;
    position: relative;
    display: flex;
    justify-content: space-between;

    @include r(1200) {
      padding: 70px 90px 150px;
    }

    @include r(992) {
      position: relative;
    }

    @include r(768) {
      padding: 50px 40px;
    }

    @include r(420) {
      flex-direction: column;
      justify-content: flex-start;
    }
  }

  &__main-article {
    width: 75%;
    margin-right: 120px;
    position: relative;

    @include r(1220) {
      margin-right: 60px;
    }

    @include r(992) {
      width: 100%;
    }

    @include r(420) {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__heading {
    font-weight: normal;
    margin-bottom: 36px;
    position: relative;

    @include r(640) {
      margin-bottom: 26px;
    }
  }

  &__back {
    position: absolute;
    display: inline-block;
    width: 17px;
    height: 24px;
    cursor: pointer;
    left: -47px;
    top: 32px;

    @include r(768) {
      left: -32px;
      top: 33px;
    }

    @include r(640) {
      top: 29px;
    }

    &::before, &::after {
      display: block;
      position: absolute;
      content: '';
      width: 17px;
      height: 2px;
      background: #1f2ee9;
    }

    &::before {
      transform: rotate(-45deg);
      left: 0;
      top: 5px;
    }

    &::after {
      transform: rotate(45deg);
      left: 0;
      top: 16px;
    }
  }

  &__heading-date {
    color: #bbc2d0;
    font-size: 13px;
    line-height: 19px;
    letter-spacing: 0.5px;
    text-transform: uppercase;
    font-weight: normal;
  }

  &__heading-main {
    color: #0d105b;
    font-size: 34px;
    font-weight: normal;
    margin-bottom: 20px;

    @include r(640) {
      line-height: 43px;
    }
  }

  &__heading-description {
    color: #000;
    font-size: 23px;
    line-height: 36px;
  }

  &__text {
    p {
      color: #000;
      opacity: 0.6;
      font-size: 16px;
      line-height: 24px;
      font-weight: normal;
    }
  }

  &__main-sidebar {
    padding-top: 19px;
    width: 25%;

    @include r(992) {
      position: absolute;
      right: 0;
      top: 0;
      background: #fff;
      height: 100%;
      box-shadow: 0 5px 9px rgba(31, 46, 233, 0.08);
      padding: 90px 30px 30px 60px;
      width: 330px;
      transition: 0.3s;
      transform: translateX(270px);

      &:hover {
        transform: translateX(0);
      }
    }

    @include r(640) {
      padding-top: 70px;
    }

    @include r(420) {
      position: static;
      transform: translateX(0);
      box-shadow: none;
      width: 100%;
      padding: 0;
      background: transparent;
      height: auto;
    }
  }

  &__sidebar-post {
    margin-bottom: 24px;
  }

  &__sidebar-post-title {
    font-size: 16px;
    color: #1F2EE9;
    line-height: 21px;
    letter-spacing: 0.5px;
    transition: all 0.2s;
    cursor: pointer;
    margin-top: 5px;
    text-decoration: none;

    &:hover {
      color: #000;
    }
  }
}
</style>



